// Donnes prives, stockes uniquement en local

var localforage = require('localforage')

module.exports = function () {
    this.supprimer = function () {
        localforage
            .dropInstance()
            .then(function () {
                console.debug('Les donnes personnelles ont t supprimes')
            })
            .catch(function (error) {
                console.error(
                    'Erreur lors de la suppression des donnes personnelles ' + error
                )
            })
    }

    this.charger = function (profil) {
        return localforage.getItem('mes_infos').then(
            function (data) {
                if (data !== null) {
                    console.debug('Donnes locales:')
                    console.log(data)
                    profil.fillData(data)
                } else {
                    console.debug('Pas de donnes locales pour linstant')
                }
                var customLoadingEvent = document.createEvent('CustomEvent')
                customLoadingEvent.initCustomEvent('dataLoaded', true, true, data)
                document.dispatchEvent(customLoadingEvent)
            },
            function (error) {
                console.error('Erreur de chargement des donnes locales ' + error)
            }
        )
    }

    this.enregistrer = function (profil) {
        return localforage
            .setItem('mes_infos', profil.getData())
            .then(function (data) {
                console.debug('Les rponses au questionnaire ont bien t enregistres')
                console.debug(data)
            })
            .catch(function (error) {
                console.error(
                    'Les rponses au questionnaire nont pas pu tre enregistres'
                )
                console.error(error)
            })
    }
}
